import { inject, observer } from 'mobx-react';
import * as React from 'react';
import injectSheet, { WithSheet } from 'react-jss';
import { Link } from 'react-router-dom';
import CardSubtitle from 'reactstrap/lib/CardSubtitle';
import CardText from 'reactstrap/lib/CardText';
import IziviContent from '../layout/IziviContent';
import { Theme } from '../layout/theme';
import { ApiStore } from '../stores/apiStore';
import createStyles from '../utilities/createStyles';

const styles = (theme: Theme) =>
  createStyles({
    page: {
      '& p': {
        textAlign: 'justify',
      },
      '& ul': {
        paddingLeft: 2 * theme.layout.baseSpacing,
        paddingRight: 2 * theme.layout.baseSpacing,
        textAlign: 'justify',
      },
    },
  });

interface Props extends WithSheet<typeof styles> {
  apiStore?: ApiStore;
}

@inject('apiStore')
@observer
class HomeInner extends React.Component<Props> {
  render() {
    const { classes } = this.props;
    return (
      <IziviContent className={classes.page} card showBackgroundImage>
        <CardSubtitle>
          <span style={{ fontSize: '3rem', fontWeight: 'bold' }}>iZivi</span> ist ein Tool der SWO zur Erfassung und Planung von
          Zivildienst-Einstzen
        </CardSubtitle>
        <CardText>
          Seit 1996 knnen Militrpflichtige, die den Militrdienst aus Gewissensgrnden ablehnen, einen zivilen Ersatzdienst leisten. Die
          SWO hat den Zivildienst mitgestaltet und bietet Zivildienstleistenden eine Vielzahl von sinnvollen Einsatzmglichkeiten zugunsten
          einer nachhaltigen Entwicklung.
        </CardText>
        {this.props.apiStore!.isLoggedIn || (
          <>
            <CardText>
              Bist du das erste Mal bei uns und mchtest einen Einsatz planen? Dann kannst du dich ber folgenden Link{' '}
              <Link to={'/register'}>registrieren</Link>
            </CardText>
            <CardText>
              Falls du uns bereits bekannt bist, kannst du dich hier <Link to={'/login'}>anmelden</Link>
            </CardText>
          </>
        )}
      </IziviContent>
    );
  }
}

export const Home = injectSheet(styles)(HomeInner);
